#blocklyDiv {
    height: 100%;
    width: 70%;
    position: absolute;
    left: 0;
    bottom: 0;
    color: black;
}

#codeOutput {
  position: absolute;
  width: 30%;
  /* flex: 1; */
  height: 100%;;
  right: 0;
  top: 0;
  background-color: rgb(26, 26, 26);
  color: white;
  padding: 16px;
  z-index: 98;
}

#button_download {
  position: absolute;
  bottom: 24px;
  right: 24px;
  z-index: 99;

  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  border: none;
  
  font-size: medium;
  width: 48px;
  height: 48px;
  cursor: pointer;

  background-color: DodgerBlue;
  color: white;
}

#button_download:hover {
  background-color: RoyalBlue;
}